.catalog__media {
  width: 280px;
  margin: 0 auto;
}

.catalog__wraper {
  width: 280px;
  margin: 0 auto;
}

.catalog__title {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  margin-top: 37px;
}

.catalog__deckriptor {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
}


@media (min-width: @tablet-width) {
  .catalog__wraper {
    width: 458px;
  }

  .catalog__title {
    font-size: 30px;
  }

  .catalog__deckriptor {
    font-size: 13px;
  }
}

